// SECTION press :: START

.press {
  overflow: hidden;
  position: relative;
  padding-top: 56px;
  padding-bottom: 197px;
  background-color: #ffffff;
  @include adaptive(1023) {
    padding-bottom: 55px;
  }

  &__bg {
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    img {
      position: absolute;
      display: block;
      max-width: 100%;
      height: auto;

      &:nth-of-type(1) {
        top: 230px;
        left: 53px;
      }
      &:nth-of-type(2) {
        right: -50px;
        top: calc(20% - 10px);
      }
    }
  }

  &__wrapper {
    z-index: 2;
    position: relative;
  }

  &__title {
    padding-left: 29px;
    padding-bottom: 86px;
    letter-spacing: -0.8px;
    text-shadow: none;
    @include adaptive(1023) {
      padding-bottom: 56px;
    }
    @include adaptive(767) {
      padding-left: 15px;
    }

    &:before {
      position: relative;
      top: 3px;
      left: -44px;
      @include adaptive(767) {
        left: -24px;
      }
    }
  }

  &__box {
    &-wrapper {
      width: 100%;
      max-width: 1023px;
      @include adaptive(767) {
        flex-direction: column;
      }

      > div {
        padding-bottom: 80px;
        animation-fill-mode: both;
        animation-duration: 1s;
        animation-name: c-fadeIn;
        @include adaptive(767) {
          padding-bottom: 50px;
        }

        &:nth-last-of-type(2) {
          @include adaptive(1023) {
            padding-bottom: 40px;
          }
          @include adaptive(767) {
            padding-bottom: 50px;
          }
        }
        &:last-of-type {
          padding-bottom: 0;
        }

        @keyframes c-fadeIn {
          from {opacity: 0;}
          to {opacity: 1;}
        }
      }
    }
  }

