  .card {
    padding-right: 15px;
    border: none;
    border-radius: 0;
    background-color: transparent;
    @include transition(transform);
    @include adaptive(767) {
      flex-direction: column !important;
      padding-right: 0;
    }

    &:hover {
      transform: translateY(-15px);
      @include transition(transform);

      .card {
        &-head {
          img {
            transform: scale(1.1);
            @include transition(transform 0.45s);
          }
        }
      }

      .icon-font {
        transform: translateX(50%);
        @include transition(transform);
      }
    }

    &--reverse {
      padding-right: 0;
      padding-left: 15px;
      justify-content: flex-end;
      @include adaptive(767) {
        padding-left: 0;
      }

      .card {
        &-body {
          padding-left: 0;
          padding-right: 90px;
          @include adaptive(1023) {
            padding-right: 30px;
          }
          @include adaptive(767) {
            padding-left: 15px;
            padding-right: 15px;
          }
        }
      }
    }

    &-head {
      overflow: hidden;
      position: relative;
      width: 100%;
      max-width: 453px;
      height: 302px;
      @include adaptive(1023) {
        max-width: 55%;
      }
      @include adaptive(767) {
        max-width: 100%;
      }

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 0;
        object-fit: cover;
        object-position: center;
        @include transition(transform 0.55s);
      }
    }

    &-body {
      flex: unset;
      padding: 0;
      padding-left: 67px;
      @include adaptive(1279) {
        flex: 1;
      }
      @include adaptive(1023) {
        padding-left: 30px;
      }
      @include adaptive(767) {
        padding-top: 15px;
        padding-left: 15px;
        padding-right: 15px;
      }
    }

    &-text {
      padding-bottom: 17px;

      p {
        min-width: 58px;
        height: 30px;
        padding: 5px 14px;
        letter-spacing: 0.5px;
        font-size: 16px;
        line-height: 20px;
        text-align: center;
        color: #FFFFFF;
        font-weight: 700;
        background: linear-gradient(270deg, #4A00A8 0%, #AF0189 25%, #4A00A8 50%, #AF0189 100%);
        background-size: 200% 200%;
        animation: animatedGradient 5s ease-in-out alternate infinite;
        @include adaptive(1023) {
          font-size: 14px;
        }
      }

      @keyframes animatedGradient {
        0% {
          background-position: 0 25%;
        }
        50% {
          background-position: 100% 50%;
        }
        100% {
          background-position: 0 25%;
        }
      }
    }

    &-title {
      width: 100%;
      max-width: 383px;
      padding-bottom: 10px;
      font-size: 30px;
      font-weight: 700;
      line-height: 33px;
      color: #0C0C0C;
      @include adaptive(1023) {
        font-size: 26px;
        line-height: 30px;
      }
      @include adaptive(767) {
        max-width: 100%;
      }
    }

    .icon-font {
      color: #0c0c0c;
      font-size: 14px;
      @include transition(transform);
    }
  }
}

